import { ADD,DEL,MODIFY } from "./constant";
// 返回

export const mapStateToProps = (state) => {
  // 定义messages为plansreducer返回值
  return {messages: state.plans}
};

export const mapDispatchToProps = (dispatch) => {
  return {
    submitNewMessage: (message,id) => {
      dispatch(addMessage(message,id))
    },
    delMessage:(id) =>{
      dispatch(deleteMessage(id))
    },
    modifyMessage:(message,id) =>{
      dispatch(modifyMessage(message,id))
    }
  }
};
// 返回action
export const addMessage = (message,id) => {
    return {
      type: ADD,
      id: id,
      message: message
    }
  };

export const deleteMessage = (id) => {
  return {
    type: DEL,
    id: id
  }
};
export const modifyMessage=(message,id)=>{
  return {
    type:MODIFY,
    id: id,
    message: message
  }
}
  