import React, { Component } from 'react';
import {mapStateToProps, mapDispatchToProps} from '../../../redux/action'
import { connect } from 'react-redux';
import {Button} from 'antd'

class List extends Component {
    constructor(props) {
      super(props);
      this.state = {
        input: '',
        // 点击修改按钮，editable==true ->输入框变红，表示处于修改状态
        editable:false,
        modifyId:0
        // messages: []
        // messages通过props属性访问
      }

    }
    handleChange=(event)=> {
      this.setState({
        input: event.target.value
      });
    }

    // 构造-action中的函数
    submitMessage=()=> {
      if(this.state.input!='')
      { 
        if(this.state.editable)
        {
          this.props.modifyMessage(this.state.input,this.state.modifyId)
        }
        else
        {
          this.props.submitNewMessage(this.state.input,this.props.messages.length);
        }
        
        this.setState({
          input: '',
          editable:false,
          modifyId:0
        });
      }    
    }
    delMessage=(id)=>{
      this.props.delMessage(id)
    }
    // 
    modifyMessage=(id)=>{
      this.setState({
        editable:true,
        modifyId:id
      });
     
    }

    // TODO！
    
    // TODO！

    render() {
      return (
        <div>
          <hr></hr>
          <h2>清单Plans——实现增删改查</h2>
          <input style={{borderColor:this.state.editable?'red':'black'}} value={this.state.input} onChange={this.handleChange}/>
          <Button style={{color:this.state.editable?'red':'black'}} onClick={this.submitMessage} children={this.state.editable?"确认修改":"确认提交"}></Button>
          
          {/* 方法1.html实现修改 contentEditable='true' */}
          <ul>
            {this.props.messages.map((messageObj, idx) => {
                return (
                   <div key={idx}><li key={idx}>{messageObj.message}</li><Button onClick={()=>this.delMessage(messageObj.id)}>删除</Button>&nbsp;<Button onClick={()=>this.modifyMessage(messageObj.id)}>修改</Button></div>
                  )
            })}
          </ul>
        </div>
      );
    }
  };

// 每个UI组件都要connect
export default connect(mapStateToProps, mapDispatchToProps)(List);

  
