import {ADD,DEL,MODIFY} from './constant'
export const messageReducer = (state = [], action) => {
    switch (action.type) {
      case ADD:
        // console.log(action.id)
        return [
          ...state,
          {message:action.message,id:action.id}
        ];
      case DEL:
        return [ ...state.slice(0, action.id), ...state.slice(action.id + 1, state.length)];
      case MODIFY:
          // 不能直接修改state
          let newstate=state
          newstate[action.id]={message:action.message,id:action.id}
          return newstate 
      default:
        return state;
    }
  };
  